@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400;500;600;700&display=swap");
.outline {
  border: 1px solid #ff9c00;
}

button {
  margin: 8px 4px;
  padding: 10px 30px;
  border: none;
  border-radius: 100px;
  color: white;
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
}
