.subHeading {
  font-size: 48px;
  font-weight: bold;
  color: #545a5f;
  margin-bottom: 20px;
}

.grey-text {
  color: #545a5f !important;
}

.heading-div {
  margin: 40px auto;
  position: relative;
}

.heading-div p {
  margin: 0;
}

.light-heading {
  color: rgba(170, 173, 177, 0.22);
  /* color: #545a5f23; */
  font-weight: bold;
  font-size: 66px;
  position: absolute;
  top: -40px;
}

@media screen and (max-width: 620px) {
  .subHeading {
    font-size: 36px;
  }
  .light-heading {
    font-size: 54px;
  }
}
