.circle-div {
  height: 50px;
  margin: 10px 6px;
  width: 50px;
  border: 2px solid white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact-bg {
  /* display: flex; */
  /* align-items: center; */
  /* justify-content: space-between; */
  color: white;
  min-height: 40vh;
  margin-top: 4rem;
  clip-path: polygon(0 0, 100% 10%, 100% 100%, 0% 100%);
  padding: 10rem;
  background-image: linear-gradient(rgba(0, 7, 10, 0.85), rgba(0, 7, 10, 0.85)),
    url("../../assets/images/contact.jpg");
  background-position: center;
  background-attachment: fixed;
  object-fit: cover;
}

.footer {
  background-color: #070f13;
  color: #ff9c00;
  padding: 1rem 8rem;
}
.copyright-text {
  color: #545a5f;
}

@media screen and (max-width: 650px) {
  .contact-bg {
    padding: 4rem 2rem;
    min-height: 40vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .footer {
    padding: 1rem 2rem;
  }
}
