.heroContent {
  background-image: linear-gradient(
      #070f13,
      rgba(0, 0, 0, 0.4),
      rgba(0, 0, 0, 0.8)
    ),
    url("../../assets/images/intro-bg.jpg");
  height: 100vh;
  /* position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: -1; */
  background-position: center;
  /* display: flex; */
  /* align-items: center; */
  top: 0;
  /* justify-content: center; */
}

.heroInfo {
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.btnGroup {
  display: flex;
  justify-content: center;
}

.claimBtn {
  margin: 8px 4px;
  padding: 10px 30px;
  background: transparent;
  border: 2px solid white;
  border-radius: 100px;
  color: white;
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
}
.stakeBtn {
  margin: 8px 4px;
  padding: 10px 30px;
  background: #ff9c00;
  border: none;
  border-radius: 100px;
  color: white;
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
}

.heroContent h1 {
  font-size: 100px;
  color: white;
}

.hero {
  margin-bottom: 80px;
}

.about {
  padding: 4rem 10rem 6rem;
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  margin-bottom: 40px;
  position: relative;
  /* height: 20vh; */
  clip-path: polygon(0 0, 100% 0%, 100% 80%, 0% 100%);

  color: #a4aaad !important;
  background-image: url("../../assets/images/pattern.png");
}

.legioDao {
  font-size: 36px;
}

.aboutInfo {
  font-size: 24px;
}

.about button {
  margin: 8px 4px;
  padding: 10px 30px;
  background: transparent;
  border: 2px solid #ff9c00;
  border-radius: 100px;
  color: #ff9c00;
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
}

@media screen and (max-width: 1000px) {
  .about {
    padding: 4rem 2rem 6rem;
  }
}

@media screen and (max-width: 600px) {
  .heroInfo h1 {
    text-align: center;
    font-size: 50px;
  }
  .heroContent {
    height: 80vh;
  }
}
