.navbar {
  background-color: transparent;
  padding-top: 10px;
  position: sticky;
  top: 0;
  z-index: 10000000000;
  font-family: "Roboto Mono", monospace !important;
  background-color: #070f13;
}

.navbarFlex {
  display: flex;
  width: 80%;
  margin: auto;
  align-items: center;
  justify-content: space-between;
}

.navbar ul {
  height: 50px;
  align-items: center;
  display: flex;
  list-style: none;
}

.brand img {
  width: 200px;
}

.navbarFlex ul li {
  cursor: pointer;
  margin: 10px 6px;
  transition: 0.3s;
  font-size: 12px;
  padding: 6px 10px;
  color: #bbb;
  border-radius: 100px;
  background-color: rgba(134, 134, 134, 0.13);
}

.active {
  border-radius: 100px;
  color: white !important;
  background-color: #ff9c00 !important;
}

.mobileMenu {
  transition: 0.4s;
  position: absolute;
  left: 0;
  right: 0;
  z-index: -1000;
  background-color: #070f13;
}

.mobileMenu ul {
  height: auto;
  padding: 1rem;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column !important;
}
.mobileMenu ul li {
  padding: 0;
  color: #bbb;
  border-radius: 100px;
  background-color: transparent;
  /* background-color: rgba(134, 134, 134, 0.13); */
}

a {
  text-decoration: none;
  color: #bbb !important;
}

.connect-modal-ul {
  flex-direction: column;
  padding: 0;
  height: auto !important;
}
.connect-modal-ul li {
  margin: 6px 10px;
  display: block;

  color: white;
  padding: 8px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  background-color: rgb(35, 35, 35);
}

.modal-body {
}

.modal-content {
  color: white;
  font-family: sans-serif;
  background-color: rgba(30, 30, 30);
}

.wallet-content {
  border:0;
  background-color:#003C9D;
  color:#fff;border-radius:10px;
}
