.tokenCard {
  text-align: center;
  margin: 30px 0;
  padding: 10px 20px;
}

.tokenCard i {
  font-size: 48px;
  color: #545a5f;
  margin-bottom: 10px;
}

.token {
  color: #a4aaad !important;
  width: 80%;
  margin: auto;
}

.tokenTitle {
  font-size: 22px;
  color: #545a5f;
}

.tokenInfo {
  color: #8e969c;
}

@media screen and (max-width: 600px) {
  .token {
    width: 90%;
  }
}
