.yellow-div {
  background-color: #ff9c00;
  height: 60px !important;
  width: 60px !important;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px;
}

.yellow-div i {
  font-size: 22px;
}
.battleNomics {
  background-image: linear-gradient(rgba(0, 7, 10, 0.85), rgba(0, 7, 10, 0.85)),
    url("../../assets/images/nomics.jpg");
  background-position: center;
  min-height: 100vh;
  background-attachment: fixed;
  clip-path: polygon(0 0, 100% 4%, 100% 96%, 0% 100%);
  margin-bottom: 5rem;
  padding: 2rem 8rem;
}

.battleNomics .headline {
  font-size: 48px;
  color: white;
}

.battleCard p {
  margin: 0;
  font-size: 20px;
  font-weight: 600;
}

.battleCard {
  color: white;
  margin: 14px;
}

@media screen and (max-width: 1000px) {
  .battleNomics {
    padding: 4rem 2rem;
  }
}
