.stakingCard {
  background-color: #897f7f69;
  border-radius: 2px;
  margin: auto;
  padding: 30px 20px;
}

.stakingContent {
  width: 560px;
}
.staking {
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  padding: 10rem;
  align-items: center;
  clip-path: polygon(0 0, 100% 4%, 100% 96%, 0% 100%);
  background-attachment: fixed;
  background-image: linear-gradient(rgba(0, 7, 10, 0.85), rgba(0, 7, 10, 0.85)),
    url("../../assets/images/nomics.jpg");
  background-position: center;
}

.staking .btn-group {
  margin: 20px auto 40px;
  width: 100%;
}

.staking .btn-group button {
  background-color: transparent;
  border: 2px solid white;
  margin: 4px;
}

.staking span {
  font-weight: bold;
}

.staking-input button {
  margin: 0;
  text-align: center;
  color: black;
}

.staking-input input {
  padding: 10px;
  border: none;
  background-color: rgba(139, 145, 150, 0.14) !important;
}

.staking-input input:focus {
  box-shadow: none;
  border: 1px solid white !important;
}

.active-btn {
  color: #897f7f;
  background-color: white !important;
}

@media screen and (max-width: 600px) {
  .staking {
    padding: 6rem 1rem;
  }
  .stakingContent {
    width: 94%;
  }
  .staking .btn-group button {
    padding: 8px 10px;
  }
  .staking-input button {
    margin-top: 8px;
  }
}
