.claim-bg {
  color: white;
  min-height: 80vh;

  padding: 6rem;
  clip-path: polygon(0 0, 100% 0%, 100% 96%, 0% 100%);
  background-attachment: fixed;
  background-image: linear-gradient(rgba(0, 7, 10, 0.85), rgba(0, 7, 10, 0.85)),
    url("../../assets/images/nomics.jpg");
  background-position: center;
}

.claim-card {
  border: 4px solid rgba(255, 255, 255, 0.4) !important;
  display: flex;
  justify-content: space-around;
  padding: 2rem 1rem;
  align-items: center;
}

.claim-card img {
  width: 300px;
}

.grey-bg {
  background-color: rgba(255, 255, 255, 0.4);
  width: 120px;
  height: 120px;
  align-items: center;
  justify-content: center;
  display: flex;
}
.grey-bg img {
  width: 60%;
  height: 60%;
}

@media screen and (max-width: 800px) {
  .claim-card {
    text-align: center;
    flex-direction: column;
  }
}
